*{
  padding: 0;
  margin: 0;
  border: 0;
  font-size: 100%;
  scroll-behavior: smooth;
  box-sizing: border-box;
  -webkit-user-select:none;
    -moz-user-select:none;
    user-select:none;
}
.container{
  width: 100% ;
  display: flex;
  flex-direction: row;
  height: 100vh;
}
.menu{
  width: 15%;
  padding: 10px 0px 0 0px;

}
.menu .top-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.item-left{
  padding-left: 10px;
}
.menu .top-menu span{
  display:flex;
  align-items: center;
}
.logo{
  margin-right: 10px;
  width: 40px;
}
.icon-menu{
  margin-right: 20px;
}
.slogan{
  font-size: 18px;
  font-weight: bold;
}
.catalog{
  margin: 20px 0 0 0;
}
.link{
  text-decoration: none;
  color: #242B2E;
}
ul{
  list-style-type: none;
}
.catalog li{
  font-weight: bold;
  padding: 10px 0 10px 15px;
  display: block;
}
.catalog li:hover{
  background-color: #CAD5E2;
}
.catalog li span{
  margin-left: 15px;
}
.fa-icon-House{
  color: #00a8ff;
}
.fa-icon-Main{
  color: #e74c3c;
}
.fa-icon-Chart{
  color: #f1c40f;
}
.fa-icon-Logs{
  color: #FC427B;
}
/* Content is right */
.content{
  background-color: rgb(242, 239, 239);
  width: 85%;
  display: flex;
  flex-direction: column;
  border-left: 1px solid rgba(211, 211, 211, 0.7);
  height: 100vh;
  overflow: hidden;
}
.content-header{
  padding: 10px 20px 0 20px;
  background-image: linear-gradient(to right, #38aafb, #043dfaf0);
}
.Dashboard{
  color: white;
  display: flex;
  flex-direction: column;
}
.title-content{
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}
.title {
  font-size: 24px;
  margin-bottom: 5px;
  color: white;
}
.profile{
  color: white;
  display: flex;
  align-items: center;
}
.profile span{
  font-size: 14px;
  font-weight: 600;
  padding-right: 15px;
  padding-left: 15px;
}
.avatar{
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 50%;
}
.Dashboard span {
  margin-bottom: 10px;
  font-weight: bold;
}
.content-body{
  position: relative;
}
/* Mainboard */

.Mainboard{
  width: 50%;
  background-color: rgba(0, 0, 0, 0.8);
  position: relative;
  bottom: 50px;
  left: 20px;
  border-radius: 10px;
  color: white;
}
.mainboard-header{
  font-size: 20px;
  color: white;
  height: auto;
  padding: 15px 0 15px 0px;
  background-color: #00D84A;
  display: flex;
  justify-content: center;
  border-radius: 10px;  
}

.mainboard-content{
  padding: 20px auto 20px auto;
}
.value-sensor{
  display: flex;
  justify-content: space-around;
  padding-top: 20px;
}
.item-value .text{
  font-size: 20px;
  color: white;
  font-weight: bold;
}
.content-value {
  display: flex;
  align-items: center;
}
.content-value span{ 
  font-size: 30px;
  font-weight: bold;
  margin-right: 20px;
}

.device-control{
  padding: 20px auto 20px auto;
  font-weight: bold;
  margin-left: 50px;
  margin-top: 20px;
}
.device-group{
  display: flex;
  height: 50px;
}
.device-item{
  margin-top: 10px;
  padding-bottom: 20px;
  display: flex;
  align-items: center;
  margin-right: 40px;
  font-size: 20px;
}
.toggle{
  display: block;
  background-color: #00a8ff;
  width: 40px;
  height: 20px;
  margin-top: 3px;
  margin-left: 10px;
  border-radius: 10px;
}
.on{
  width: 55%;
  height: 100%;
  display: block;
  background-color:white;
  border-radius: 10px;
}
.off{
  width: 55%;
  height: 100%;
  display: block;
  background-color:#FC427B;
  border-radius: 10px;
  float: right;
}


/* Line Chart */
canvas{

  width:95% !important;
  height:100% !important;
  background-color:#242B2E;
  color: white;
  border-radius: 20px;
  position: relative;
  bottom: 50px;
  left: 20px;
}